
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Card from 'vux/src/components/card/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'

import {httpUrl} from '../../http_url';
import iView from 'iview';
import 'iview/dist/styles/iview.css';
import { Table } from 'iview';

export default {
directives: {
TransferDom
},
data () {
return {
menus: {
menu1: '退出登录'
},
showMenus: false,
pdfurls:'',
isIOS:true,
transfinite:'',      //活体检测次数  0:没有超过校验次数  1:超过校验次数
yx_ht_id:'',
idcard_name:'',
idcard_num:'',
email:'',
mailing_address:'',
phone:'',
company_name:'',
licence_number:'',
address:'',
supervise_tel:'',
people:'',
flight_price:'',
big_flight_price:'',
hotel_price:'',
big_hotel_price:'',
other_price:'',
big_other_price:'',
totalprice:'',
big_totalprice:'',
stage:'',
fenqi_price:'',
paymoney:'',
per_price:'',
year:'',
month:'',
bank_1_number:'',
bank_1_name:'',
other_info:'',
columns1:[
{
title: '委托人姓名',
key: 'name',
minWidth:100,
},
{
title: '委托人英文姓名',
key: 'engname',
minWidth:150,
},
{
title: '性别',
key: 'gender',
minWidth:80,
},
{
title: '有效身份证/护照号',
key: 'passportid',
minWidth:180,
},
{
title: '证件有效期',
key: 'cardtime',
minWidth:100,
},
{
title: '联系方式',
key: 'tel',
minWidth:120,
}],
data1:[],
columns2:[
{
title: '航段',
key: 'segment',
minWidth:100,
},
{
title: '承运航空公司',
key: 'airways',
minWidth:150,
},
{
title: '航班号',
key: 'flight',
minWidth:80,
},
{
title: '起飞时间(当地时间)',
key: 'qdate',
minWidth:160,
},
{
title: '抵达时间(当地时间)',
key: 'tdate',
minWidth:160,
},
{
title: '座位等级',
key: 'grade',
minWidth:120,
},
{
title: '行李',
key: 'baggage_weight',
minWidth:80,
}],
data2:[],
columns3:[
{
title: '预订酒店名称',
key: 'hname',
minWidth:120,
},
{
title: '预订房型',
key: 'type',
minWidth:100,
},
{
title: '入住日期',
key: 'sdate',
minWidth:100,
},
{
title: '离店日期',
key: 'edate',
minWidth:100,
},
{
title: '间夜数量',
key: 'num',
minWidth:100,
},
{
title: '用餐包含',
key: 'meal_contains',
minWidth:120,
},
{
title: '酒店接送',
key: 'hotel_pickup',
minWidth:100,
},
{
title: '特殊申请',
key: 'special_application',
minWidth:120,
}
],
data3:[],

}
},
created(){
this.getData();
},
components: {
XHeader,
PopupHeader,
Actionsheet,
XButton,
XTable,
Table
},
methods:{

getData(){
let orderid = this.$route.query.orderid;
const _this = this;
let postData = this.$qs.stringify({
orderid:orderid
})
this.$axios({
method: 'post',
url:httpUrl.previewContract,
data:postData
}).then((res)=>{
console.log(res.data)
let data =res.data.retData;
if(res.data.errCode==200){
this.yx_ht_id = data.cost.yx_ht_id;
this.idcard_name = data.traveler.idcard_name;
this.idcard_num = data.traveler.idcard_num;
this.email = data.traveler.email;
this.mailing_address = data.traveler.mailing_address;
this.phone = data.traveler.phone;
this.company_name = data.seller.company_name;
this.licence_number = data.seller.licence_number;
this.address = data.seller.address;
this.supervise_tel = data.seller.supervise_tel;
this.people = data.traveler.people;
this.flight_price = data.cost.flight_price;
this.big_flight_price = data.cost.big_flight_price;
this.hotel_price = data.cost.hotel_price;
this.big_hotel_price = data.cost.big_hotel_price;
this.other_price = data.cost.other_price;
this.big_other_price = data.cost.big_other_price;
this.totalprice = data.cost.totalprice;
this.big_totalprice = data.cost.big_totalprice;
this.stage = data.cost.stage;
this.fenqi_price = data.cost.fenqi_price;
this.paymoney = data.cost.paymoney;
this.per_price = data.cost.per_price;
this.year = data.cost.year;
this.month = data.cost.month;
this.bank_1_number = data.traveler.bank_1_number;
this.bank_1_name = data.traveler.bank_1_name;
this.other_info = data.cost.other_info;
this.data1 = data.chuxingren;
this.data2 = data.flight;
this.data3 = data.hotel;
this.transfinite = data.transfinite;

}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
goSignature(){
let step_type = this.$route.query.step_type;
let orderid = this.$route.query.orderid;
this.$router.push({
path:'/signature',
query: {
step_type: step_type,
orderid:orderid
}
});
},
goLiveFace(){
const _this = this;
let step_type = this.$route.query.step_type;
let orderid = this.$route.query.orderid;

if(this.transfinite == 1){
this.$vux.alert.show({
title: '错误提示',
content: '失败次数过多，请联系客服：0755-22211333',
onHide () {
_this.$router.push('/home');
}
})
}else {
this.$router.push({
path:'/qiniu_upload',
query: {
step_type: step_type,
orderid:orderid
}
});
}

}
}
}



